.skillsBlock {
    min-height: 100vh;
    background-color: cadetblue;
}

.skillsContainer {
    flex-direction: column;
    justify-content: flex-start;
}

.title {
    background: #e3f7f2;
}

.skills {
    border: 1px solid;
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    box-sizing: border-box;
}